import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
import {
		mapState,
		mapMutations
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import sibList from '@/components/sib-list/sib-list.vue'

export default {
	components: {tabControl,uniPopup,sibList},
	computed: {
	            ...mapState(['userInfo','orderTitle'])  
	        },
	data() {
		return {
			current: 0,
			credit_current:0,
			credit_num:0,
			order_current:0,
			order_num:0,
			credit_list:'',
			credit_type:0,
			order_list:'',
			order_q:0,
			order_details:'',
			m:false,
			order_id:'',
			plan:0,
			u_share:0,
			c_share:0,
			popup:0,
			noData:0,
			share:0,
			share_index:0,
			page:1,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			n:0,
			d:''
		};
	},
	onLoad(option) {
		uni.getSystemInfo({
			success:(res) => {
				this.Height = res.screenHeight - 400 +'px'
			}
		})
		this.request.httpRequest('api/apply/nav','GET').then(res => {
			if(res.data.code === 1000){
				this.credit_list = res.data.retval
				this.order_list = res.data.retval[this.credit_current].child
			}else{
				uni.showToast({
					title:res.data.msg,
					icon:"none"
				})
			}
		})
		let data = {
			mode:this.current,
			type:this.credit_type,
			q:this.order_q,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/apply/index',data,'GET').then(res => {
			this.order_details = res.data.retval
			if(this.order_details < 1) {
				this.noData = 1
			}else{
						this.noData = 0
					}
		})
	},
	watch:{
		current(newval,oldval){
			this.current = newval
			this.$refs.sibList.toTopClick()
			this.credit_type = 0
			this.credit_current = 0
			this.order_current = 0
			this.order_q = 0
			this.n = 0
			if(newval == 1) {
				this.u_share = 1
				this.plan = 3
				this.u_share = 3
			}else {
				// this.u_share = 0
				this.plan = 0
				// this.u_share = 0
				this.u_share = 0
			}
			let data = {
				mode:newval,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				this.order_details = res.data.retval
				if(this.order_details < 1) {
					this.noData = 1
				}else{
						this.noData = 0
					}
			})
		},
		credit_current(newval,oldval){
			this.$refs.sibList.toTopClick()
			this.c_share = newval
			this.credit_type = this.credit_list[newval].type
			this.order_current = 0
			this.order_q = 0
			this.n = 0
			if(this.current == 0){
				if(newval == 1 || newval == 2 || newval == 3){
					this.plan = 1
				}else{
					this.plan = 0
				}
				this.u_share = 0
			}else{
				this.u_share = 3
				this.plan = 3
			}
			let data = {
				mode:this.current,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				this.order_details = res.data.retval
				if(this.order_details < 1) {
					this.noData = 1
				}else{
						this.noData = 0
					}
			})
			this.request.httpRequest('api/apply/nav','GET').then(res => {
				if(res.data.code === 1000){
					this.order_list = res.data.retval[newval].child
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none"
					})
				}
			})
		},
		order_current(newval,oldval){
			this.$refs.sibList.toTopClick()
			this.order_q = this.order_list[newval].q
			this.n = 0
			let data = {
				mode:this.current,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				if(this.order_q == 6){
					this.plan = 3
					this.u_share = 3
					for (var i = 0; i < res.data.retval.length; i++) {
						if(res.data.retval[i].is_deleted == 1) {
							res.data.retval[i].status = "已删除订单"
						}
					}
				}else {
					if(this.current == 1){
						this.u_share = 3
						this.plan = 3
					}else {
						this.plan = 0
						this.u_share = 0
					}
				}
				this.order_details = res.data.retval
				if(this.order_details < 1) {
					this.noData = 1
				}else{
						this.noData = 0
					}
			})
		}
	},
	methods: {
		lower(e) {
			this.page = this.page + 1
			let data = {
				mode:this.current,
				type:this.credit_type,
				q:this.order_q,
				pageNo:this.page,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				if(!this.n) {
					uni.showLoading({
						title: '加载中...',
						mask: true
					})
						let list = res.data.retval
						if(res.data.retval.length < 1){
							this.n = 1
							uni.showToast({
								icon: 'none',
								title: '没有消息了'
							})
						}else{
							this.n = 0
							uni.showToast({
								icon: 'success',
								title: '加载成功'
							})
						}
						this.order_details = this.order_details.concat(list)
						uni.hideLoading()
				}
			})
		},
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		Uorder() {
			this.current = 0
		},
		Torder() {
			this.current = 1
		},
		credit(index) {
			if(this.credit_current !== index){
				this.credit_current = index
			}
		},
		order(index) {
			if(this.order_current !== index){
				this.order_current = index
			}
		},
		Plan(url) {
			if(url != 1){
				uni.navigateTo({
					url:"../checkprogress/checkprogress?url="+encodeURIComponent(JSON.stringify(url))
				})
			}else{
				this.popup = 1
			}
		},
		close() {
			this.$refs.popup.close()
		},
		del() {
			this.$refs.popup.close()
			let data = {
				id:this.order_id,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/delete',data,'GET').then(res => {
				uni.showToast({
					title:res.data.msg,
					icon:"none"
				})
				let data = {
					mode:this.current,
					type:this.credit_type,
					q:this.order_q,
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				this.request.httpRequest('api/apply/index',data,'GET').then(res => {
					this.order_details = res.data.retval
					if(this.order_details < 1) {
						this.noData = 1
					}else{
						this.noData = 0
					}
				})
			})
		},
		toDelete(order_id){
			this.order_id = order_id
			this.$refs.popup.open()
		},
		toShare(index) {
			var s
			let data = {
				mode:this.current,
				type:this.credit_type,
				q:this.order_q,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/apply/index',data,'GET').then(res => {
				this.order_details = res.data.retval
				s = this.order_details[index].link_href
				if(s != 1) {
					this.share = 1
					this.share_index = index
				}else if (s == 1){
					this.popup = 1
				}
				if(this.order_details < 1) {
					this.noData = 1
				}else{
							this.noData = 0
						}
			})
		},
		shareCancel() {
			this.share = 0
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 0,
			    href: this.order_details[this.share_index].link_href,
				title: this.order_details[this.share_index].realname + "邀请您查询" + this.order_details[this.share_index].name + "办卡进度",
				imageUrl: this.order_details[this.share_index].thumb,
				summary: this.order_details[this.share_index].realname + "邀请您查询" + this.order_details[this.share_index].name + "办卡进度",
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
					console.log("fail:" + JSON.stringify(err));
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		// 刷新touch监听
		touchstart: function(e) {
		    this.$refs.sibList.refreshStart(e);
		},
		touchmove: function(e) {
		    this.$refs.sibList.refreshMove(e);
		},
		touchend: function(e) {
		    this.$refs.sibList.refreshEnd(e);
		},
		isRefresh: function() {
		    
		},
		scrolltolowerFn: function() {
		    this.page = this.page + 1
		    let data = {
		    	mode:this.current,
		    	type:this.credit_type,
		    	q:this.order_q,
		    	pageNo:this.page,
		    	user_id: this.userInfo.user_id,
		    	token: this.userInfo.token,
		    	sign: md5(this.userInfo.user_id+this.userInfo.token)
		    }
		    this.request.httpRequest('api/apply/index',data,'GET').then(res => {
		    	if(!this.n) {
		    		uni.showLoading({
		    			title: '加载中...',
		    			mask: true
		    		})
		    			let list = res.data.retval
		    			if(res.data.retval.length < 1){
		    				this.n = 1
		    				uni.showToast({
		    					icon: 'none',
		    					title: '没有消息了'
		    				})
		    			}else{
		    				this.n = 0
		    				uni.showToast({
		    					icon: 'success',
		    					title: '加载成功'
		    				})
		    			}
		    			this.order_details = this.order_details.concat(list)
		    			uni.hideLoading()
		    	}
		    })
		},
		...mapMutations(['toorder'])
	},
	onShow() {
		this.credit_current = this.orderTitle
	},
	onHide() {
		this.toorder(0)
	}
}
